.userInfo {
    display: grid;
    grid-template-columns: 6fr 6fr;
    gap: 10px;

    &__name {
        background: white;
        border: 1px solid lightgrey;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5px;
    }

    &__photoUpload::-webkit-file-upload-button {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        background: rgb(82, 148, 226);
        color: white;
        border: 1px solid lightgray;
        // font-weight: 600;
        cursor: pointer;
    }

    &__name h2 {
        font-size: 18px;
        font-weight: 600;
        margin: 10px;
    }

    &__photoBlock {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: 128px;
    }

    &__photoBlock img {
        max-width: 128px;
    }

    &__photoBlock input {
        width: 100%;
    }

    &__block {
        background: white;
        border: 1px solid lightgrey;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        font-size: 18px;
        font-weight: 500;
    }
}










